import React, { Component } from 'react';
import Event from '../../components/Event/Event';
import './Events.css';

const testEvents = [
  {id: 1234, title: "Hello World", date: "Never", info: "Some Stuff"},
  {id: 2345, title: "Hello Planet", date: "Sometime", info: "More Stuff"},
  {id: 3456, title: "Goodbye World", date: "June 3, 2017", info: "Cool Stuff"},
  {id: 4567, title: "Goodbye Planet", date: "May 6, 2017", info: "Cooler Stuff"},
  {id: 5678, title: "Patterns are Cool", date: "May 64, 2098", info: "Kune Onla pongo"},
]

//App contains the header and then renders the Router which contains the css to render the actual view
class Events extends Component {
  eventsToComponenets(array){
    return array.map((event) =>
      <Event key={event.id} id={event.id} title={event.title} date={event.date} info={event.info}/>
    );
  }

  render() {
    const eventList = this.eventsToComponenets(testEvents);
    return (
      <div className="events-container">
        {eventList}
      </div>
    );
  }
}

export default Events;



