import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import './HeaderOption.css'

class HeaderOption extends Component{
  render(){
    return(
      <div className="header-option" onClick={() => this.props.history.push(this.props.to)}>
        <p>{this.props.text}</p>
      </div>
    )
  }
}

export default withRouter(HeaderOption);


