import React, { Component } from 'react';
import './header.css'
import HeaderOption from '../HeaderOption/HeaderOption';

export default class Header extends Component{
  render(){
    return(
      <div className="header">
        <div className="header-container">
          <div className="header-title">
            <h3><span>MELROSE</span> High School Orchestra</h3>
          </div>
          <div className="header-options">
            <HeaderOption text="Home" to="/"/>
            <HeaderOption text="Events" to="/events"/>
            <HeaderOption text="Test" to="/test"/>
            <HeaderOption text="Calendar" to="/calendar"/>
            <HeaderOption text="Break" to="/skdjflaksdlkf"/>
          </div>
        </div>
      </div>
    )
  }
}


