import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import './Event.css';

//Event renders a div that contains the title, id, and info from an event
//Clicking on the event title links to /event/:id
class Event extends Component {
  render() {
    let {
      title,
      id,
      date,
      info
    } = this.props;
    console.log(title, id, date, info);
    return (
      <div className="event-container">
        <h3 className="event-title"><Link className='event-title' to={`/events/${id}`}>{title}</Link></h3>
        <p className="event-date">Posted on {date}</p>
        <p className="event-info">{info}</p>
      </div>
    );
  }
}

Event.PropTypes = {
  title: PropTypes.string.isRequired,
  id: PropTypes.number.isRequired,
  date: PropTypes.string.isRequired,
  info: PropTypes.string.isRequired
};

export default Event;



