import React, { Component } from 'react';
import './EventPage.css';

const testEvents = [
  {id: 1234, title: "Hello World", date: "Never", info: "Some Stuff"},
  {id: 2345, title: "Hello Planet", date: "Sometime", info: "More Stuff"},
  {id: 3456, title: "Goodbye World", date: "June 3, 2017", info: "Cool Stuff"},
  {id: 4567, title: "Goodbye Planet", date: "May 6, 2017", info: "Cooler Stuff"},
  {id: 5678, title: "Patterns are Cool", date: "May 64, 2098", info: "Kune Onla pongo"},
]

//App contains the header and then renders the Router which contains the css to render the actual view
class EventPage extends Component {
  render() {
    let title, info, date;
    let found = false;
    testEvents.forEach((event) => {
      if(parseInt(this.props.match.params.id, 10) === event.id){
        found = true;
        date = event.date;
        title = event.title;
        info = event.info;
      }
    });
    return (
      found ?
        <div className="event-page-container">
          <h3 className="event-page-title">{title}</h3>
          <p className="event-page-date">Posted on {date}</p>
          <p className="event-page-info">{info}</p>
        </div>
        :
        <div>
          <p>Event Not Found</p>
        </div>
    );
  }
}

export default EventPage;



