import React, { Component } from 'react';
import './Calendar.css';
import BigCalendar from 'react-big-calendar';
import moment from 'moment';
import { withRouter } from 'react-router-dom';
import 'react-big-calendar/lib/css/react-big-calendar.css'

BigCalendar.momentLocalizer(moment);

const testEvents = [
  {id: 1234, title: "Hello World", date: "Never", info: "Some Stuff", start: new Date(), end: new Date(2017, 8, 15, 13)},
  {id: 2345, title: "Hello Planet", date: "Sometime", info: "More Stuff", start: new Date(), end: new Date(2017, 8, 15, 14)},
  {id: 3456, title: "Goodbye World", date: "June 3, 2017", info: "Cool Stuff", start: new Date(), end: new Date(2017, 8, 15, 15)},
]

//App contains the header and then renders the Router which contains the css to render the actual view
class Calendar extends Component {
  constructor(props){
    super(props);
    this.handleSelect = this.handleSelect.bind(this);
  }

  handleSelect(event){
    this.props.history.push(`/events/${event.id}`);
  }

  render() {
    return (
      <div className="calendar-container">
        <BigCalendar
          selectable
          {...this.props}
          events={testEvents}
          views={['month', 'day']}
          onSelectEvent={event => this.handleSelect(event)}
        />
      </div>
    );
  }
}

export default withRouter(Calendar);



