import React, { Component } from 'react';
import './App.css';
import Header from '../components/Header/Header';
import Router from './Router';

//App contains the header and then renders the Router which contains the css to render the actual view
class App extends Component {
  render() {
    return (
      <div className="App">
        <Header/>
        <Router/>
      </div>
    );
  }
}

export default App;



